@import '/src/App';

.generalContainer {
    display: flex;
    justify-content: center;
    position: relative;
    background-color: var(--background);
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("../assets/images/bg.jpg");
    &:after {
        position: absolute;
        background: rgba(0, 0, 0, 0.4);
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        content: "";
        z-index: 10;
    }
}

.contentContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 50px 0;
    width: 100%;
}

.photo {
    width: 13vw;
    height: 13vw;
    min-width: 170px;
    min-height: 170px;
    max-width: 15vw;
    max-height: 15vw;
    object-fit: cover;
    border-radius: 50%;
    border: 3px solid var(--misc);
    background-color: #fff;
    @include no-select;
    z-index: 20;
}

.firstLine {
    font-size: 56px;
    font-weight: 600;
    font-family: "Hind Siliguri", sans-serif;
    line-height: 80px;
    text-align: center;
    color: var(--text);
    margin-top: 30px;
    z-index: 20;
}

.secondLine {
    font-size: 24px;
    font-family: "Lato", sans-serif;
    letter-spacing: 2px;
    line-height: 1.1;
    text-align: center;
    opacity: .66;
    color: var(--text);
    z-index: 20;
}